import uuid4 from "uuid4";
export default function todoListReducer(state = [], action) {
  switch (action.type) {
    case "ADD":
      return [
        ...state,
        {
          id: uuid4(),
          values: action.values.clubName,
            show:true,
            crossed:false
        }
      ];
    case "EDIT":
      return [...state].map((v, i, arr) =>
        v.id === action.id
          ? {
              ...v,
              clicked: true
            }
          : v
      );
    case "CLOSE":
      return [...state].map((v, i, arr) =>
        v.id === action.id
          ? {
              ...v,
              clicked: false
            }
          : v
      );
    case "NEWVALUE":
      if (action.value.InputInButtons.trim() === "") {
        return state.filter(todo => todo.id !== action.id);
      }
      return [...state].map(v =>
        v.id === action.id
          ? {
              ...v,
              values: action.value.InputInButtons,
              clicked: false
            }
          : v
      );
      case "CROSS":

          return [...state].map((v, i, arr) =>
              v.id === action.id
                  ? {
                      ...v,
                      crossed:!v.crossed
                  }
                  : v
          );
      case "ACTIVE":
          return [...state].map((v, i, arr) =>
              v.crossed===true?{
                  ...v,show:false
              }:{...v,show:true});
      case "COMPLETED":
          return [...state].map((v, i, arr) =>
              v.crossed===true?{
                  ...v,show:true
              }:{...v,show:false});
      case "SHOWALL":
          return [...state].map((v, i, arr) =>v?{...v,show:true}:v);

    case "DELETE":
      return state.filter(todo => todo.id !== action.id);

    default:
      return state;
  }
}
