import React from "react";


const AfterAdd = ({v,handleEdit,handleDelete}) => {
  return (
    <div
      className="btn-group col-auto pr-0"
      role="group"
      aria-label="Basic example"
    >
      <button
        style={{ width: 65 }}
        type="button"
        className="btn btn-primary"
        onClick={e=>handleEdit(v,e)}
      >
        Edit
      </button>
      <button
        style={{ width: 65 }}
        type="button"
        className="btn btn-danger p-0 "
        onClick={e => handleDelete(v, e)}
      >
        Delete
      </button>
    </div>
  );
};

export default React.memo(AfterAdd);