import { takeLatest, call, put, all } from "redux-saga/effects";
import axios from "axios";

// watcher saga: watches for actions dispatched to the store, starts worker saga
export function* watcherSaga() {
  yield all([
    takeLatest("API_CALL_REQUEST", workerSaga),
    takeLatest("API_CALL_REQUEST_JOKE", workerSagaJoke)
  ]);
}
function fetchDog() {
  return axios({
    method: "get",
    url: "https://dog.ceo/api/breeds/image/random"
  });
}
function* workerSaga() {
  try {
    const response = yield call(fetchDog);
    const dog = response.data.message;

    // dispatch a success action to the store with the new dog
    yield put({ type: "API_CALL_SUCCESS", dog });
  } catch (error) {
    yield put({ type: "API_CALL_FAILURE", error });
  }
}

function fetchJoke() {
  return axios({
    method: "get",
    url: "https://api.icndb.com/jokes/random"
  });
}
function* workerSagaJoke() {
  try {
    const response = yield call(fetchJoke);
    const joke = response.data.value.joke;

    // dispatch a success action to the store with the new dog
    yield put({ type: "API_CALL_SUCCESS_JOKE", joke });
  } catch (error) {
    yield put({ type: "API_CALL_FAILURE_JOKE", error });
  }
}
