// action types
const API_CALLJOKE_REQUEST = "API_CALL_REQUEST_JOKE";
const API_CALLJOKE_SUCCESS = "API_CALL_SUCCESS_JOKE";
const API_CALLJOKE_FAILURE = "API_CALL_FAILURE_JOKE";

// reducer with initial state
const initialState = {
    fetching: false,
    joke: null,
    error: null
};

export function jokereducer(state = initialState, action) {
    switch (action.type) {
        case API_CALLJOKE_REQUEST:
            return { ...state, fetching: true, error: null };
        case API_CALLJOKE_SUCCESS:
            return { ...state, fetching: false, joke: action.joke };
        case API_CALLJOKE_FAILURE:
            return { ...state, fetching: false, joke: null, error: action.error };
        default:
            return state;
    }
}