import React, { Component } from "react";

import "./App.css";
import logo_spinner from '../ajax-loader.gif'
import { connect } from "react-redux";


class DogSaga extends Component {
    render() {
        const { fetching, joke, onRequestJoke, error } = this.props;

        return (

                <header className="App-header-joke">
                    <span className='span-joke'>{joke}</span>
                    <h1 className="App-title-joke mt-5">Welcome to Chuck Norris Saga</h1>
									{joke ? (
										<p className="App-intro-joke">Keep clicking for new jokes</p>
									) : (
										<p className="App-intro-joke">Replace the React icon with a joke!</p>
									)}

									{fetching ? (
										<button className='btn btn-block' disabled> <img src={logo_spinner} alt=''/></button>
									) : (
										<div><button className='btn btn-primary' onClick={onRequestJoke}>Request a joke</button></div>
									)}

									{error && <p style={{ color: "red" }}>Uh oh - something went wrong!</p>}
                </header>




        );
    }
}

const mapStateToProps = store => {
    return {
        fetching: store.jokereducer.fetching,
        joke: store.jokereducer.joke,
        error: store.jokereducer.error
    };
};

const mapDispatchToProps = dispatch => {
    return {
        onRequestJoke: () => dispatch({ type: "API_CALL_REQUEST_JOKE" })
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(DogSaga);