import React, { Component } from "react";
import logo from "../logo.svg";
import "./App.css";
import logo_spinner from '../ajax-loader.gif'
import { connect } from "react-redux";


class DogSaga extends Component {
    render() {
        const { fetching, dog, onRequestDog, error } = this.props;

        return (

                <header className="App-header-dog">
                    <img src={dog || logo} className={!dog?"App-logo":'img-fluid img-dog'} alt="logo" />
                    <h1 className="App-title-dog">Welcome to Dog Saga</h1>
									{dog ? (
										<p className="App-intro-dog">Keep clicking for new dogs</p>
									) : (
										<p className="App-intro-dog">Replace the React icon with a dog!</p>
									)}

									{fetching ? (
										<button className='btn btn-block' disabled> <img src={logo_spinner} alt=''/></button>
									) : (
										<div><button className='btn btn-primary' onClick={onRequestDog}>Request a Dog</button></div>
									)}

									{error && <p style={{ color: "red" }}>Uh oh - something went wrong!</p>}
                </header>




        );
    }
}

const mapStateToProps = store => {
    return {
        fetching: store.dogreducer.fetching,
        dog: store.dogreducer.dog,
        error: store.dogreducer.error
    };
};

const mapDispatchToProps = dispatch => {
    return {
        onRequestDog: () => dispatch({ type: "API_CALL_REQUEST" })
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(DogSaga);