import React from "react";
import { Field, reduxForm } from "redux-form";


const Todolist = ({ handleSubmit, pristine, reset, submitting,input}) => {
  return (
    <form onSubmit={handleSubmit}>

        <label>Automedon list</label>

        <div className="container input-group justify-content-center mr-0 pr-0">
          <Field
            className="form-control"
            name="clubName"
            type="text"
            component="input"
            label="Automedon list"
          />
          <button
            style={{ width: 130 }}
            className="btn btn-info "
            type="submit"
            disabled={pristine || submitting||(input.clubName===undefined)}
          >
            Add your task
          </button>

      </div>
    </form>
  );
};

export default reduxForm({
  form: "Todolist",

})(React.memo(Todolist));
