import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./Site/components/Layout/Layout";
import * as serviceWorker from "./serviceWorker";
import { BrowserRouter } from "react-router-dom";
import { Provider } from "react-redux";
import { watcherSaga } from "./Site/components/Content/Components/Dogsaga/sagas";

import { applyMiddleware, createStore, compose } from "redux";
import { dogreducer } from "./Site/components/Content/Components/Dogsaga/reducers";
import { jokereducer } from "./Site/components/Content/Components/Chucksaga/reducers";
import createSagaMiddleware from "redux-saga";
import todoListReducer from "./Site/components/Content/Components/Todolist/reducers/todoList";
import { reducer as formReducer } from "redux-form";
import { combineReducers } from "redux";
const sagaMiddleware = createSagaMiddleware();
const composeEnhancer = window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ || compose;
const rootReducer = combineReducers({
  todoListReducer,
  dogreducer,
  jokereducer,
  form: formReducer
});
const store = createStore(
  rootReducer,
  composeEnhancer(applyMiddleware(sagaMiddleware))
);
sagaMiddleware.run(watcherSaga);
ReactDOM.render(
  <Provider store={store}>
    <BrowserRouter>
      <App />
    </BrowserRouter>
  </Provider>,
  document.getElementById("root")
);


serviceWorker.unregister();
