import React from "react";
import { Field, reduxForm } from "redux-form";

const InputInButtons = ({
  handleSubmit,
  pristine,
  v,
  reset,
  submitting,
  close,
  handleClose
}) => {
  return (
    <form onSubmit={handleSubmit}>
      <div className="d-flex input-group justify-content-end pr-0 ml-0">
        <Field
          className="form-control"
          name="InputInButtons"
          type="text"
          component="input"
        />
        <div className="btn-group ml-3" role="group" aria-label="Basic example">
          <button
            style={{ width: 65 }}
            className="btn btn-success w-50px "
            type="submit"
            disabled={pristine || submitting}
          >
            OK
          </button>
          <button
            style={{ width: 65 }}
            type="button"
            className="btn btn-secondary"
            onClick={e => handleClose(v, e)}
          >
            Close
          </button>
        </div>
      </div>
    </form>
  );
};

export default reduxForm({
  form: "InputInButtons" // a unique identifier for this form
})(React.memo(InputInButtons));
