import React, { Component } from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import Dogsaga from "./Components/Dogsaga/components/App";
import Calendar from "./Components/Calendar/Calendar";
import Todolist from "./Components/Todolist/containers/index";
import Game from './Components/Tictactoegame/index'
import Shop from './Components/Shop/App'
import Jokesaga from "./Components/Chucksaga/components/App";
import Home from './Components/Home/App'


class Content extends Component {
  render() {
    return (
      <>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route path="/dogsaga" component={Dogsaga} />
					<Route path="/jokesaga" component={Jokesaga} />
          <Route path="/todolist" component={Todolist} />
          <Route path="/calendar" component={Calendar} />
					<Route path="/tictactoegame" component={Game} />
					<Route path="/shop" component={Shop} />
          <Redirect to="/" />
        </Switch>
      </>
    );
  }
}

export default Content;
